import { useEffect, useState } from 'react'
import { motion } from 'framer-motion'
import DashboardArtisticSimple from './pages/DashboardArtisticSimple.tsx'
import CreditPurchase from './pages/CreditPurchase.tsx'
import Settings from './pages/Settings.tsx'
import TelegramHeader from './components/TelegramHeader'
import TelegramNavigation from './components/TelegramNavigation'
import { TelegramWebApp } from './types/telegram'

type Page = 'dashboard' | 'credits' | 'settings'

function App() {
  const [currentPage, setCurrentPage] = useState<Page>('dashboard')
  const [telegramApp, setTelegramApp] = useState<TelegramWebApp | null>(null)
  const [user, setUser] = useState<any>(null)

  useEffect(() => {
    // Initialize Telegram WebApp
    if (window.Telegram?.WebApp) {
      const tg = window.Telegram.WebApp
      setTelegramApp(tg)
      
      // Configure the app
      tg.ready()
      tg.expand()
      
      // Set theme colors
      tg.headerColor = '#1976D2'
      tg.backgroundColor = '#0F172A'
      
      // Get user data
      if (tg.initDataUnsafe?.user) {
        setUser(tg.initDataUnsafe.user)
      }
      
      // Enable closing confirmation
      tg.isClosingConfirmationEnabled = true
      
      // Handle back button
      tg.BackButton.onClick(() => {
        if (currentPage !== 'dashboard') {
          setCurrentPage('dashboard')
        } else {
          tg.close()
        }
      })
    }
  }, [currentPage])

  useEffect(() => {
    // Show/hide back button based on current page
    if (telegramApp) {
      if (currentPage === 'dashboard') {
        telegramApp.BackButton.hide()
      } else {
        telegramApp.BackButton.show()
      }
    }
  }, [currentPage, telegramApp])

  const navigateToPage = (page: Page) => {
    setCurrentPage(page)
    // Haptic feedback for navigation
    if (telegramApp?.HapticFeedback) {
      telegramApp.HapticFeedback.impactOccurred('light')
    }
  }

  const pageVariants = {
    initial: { opacity: 0, scale: 0.95 },
    in: { opacity: 1, scale: 1 },
    out: { opacity: 0, scale: 1.05 }
  }

  const pageTransition = {
    type: 'spring',
    stiffness: 400,
    damping: 30
  }

  const getPageTitle = () => {
    switch (currentPage) {
      case 'dashboard': return 'NSXoChat Dashboard'
      case 'credits': return 'Credit Purchase'
      case 'settings': return 'Settings'
      default: return 'NSXoChat'
    }
  }

  const getPageSubtitle = () => {
    switch (currentPage) {
      case 'dashboard': return 'AI Chat & Management'
      case 'credits': return 'Buy Credits & Packages'
      case 'settings': return 'Account & Preferences'
      default: return undefined
    }
  }

  const renderCurrentPage = () => {
    switch (currentPage) {
      case 'dashboard':
        return (
          <DashboardArtisticSimple 
            user={user} 
            telegramApp={telegramApp}
            onNavigate={navigateToPage}
          />
        )
      case 'credits':
        return (
          <CreditPurchase 
            user={user}
            telegramApp={telegramApp}
            onNavigate={navigateToPage}
          />
        )
      case 'settings':
        return (
          <Settings 
            user={user}
            telegramApp={telegramApp}
            onNavigate={navigateToPage}
          />
        )
      default:
        return null
    }
  }

  if (!telegramApp) {
    return (
      <div className="min-h-screen bg-slate-900 flex items-center justify-center">
        <div className="text-center">
          <motion.div
            animate={{ rotate: 360 }}
            transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
            className="w-12 h-12 border-4 border-blue-500 border-t-transparent rounded-full mx-auto mb-4"
          />
          <p className="text-white text-lg font-medium">Loading NSXoChat...</p>
          <p className="text-gray-400 text-sm mt-1">Connecting to Telegram...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-slate-900 text-white flex flex-col">
      {/* Enhanced Header */}
      <TelegramHeader
        user={user}
        title={getPageTitle()}
        subtitle={getPageSubtitle()}
      />

      {/* Page Content */}
      <main className="flex-1 relative pb-20">
        <motion.div
          key={currentPage}
          initial="initial"
          animate="in"
          exit="out"
          variants={pageVariants}
          transition={pageTransition}
          className="h-full"
        >
          {renderCurrentPage()}
        </motion.div>
      </main>

      {/* Enhanced Navigation */}
      <TelegramNavigation
        currentPage={currentPage}
        onNavigate={navigateToPage}
      />
    </div>
  )
}

export default App 